
.fade-enter-active, .fade-leave-active {
  transition: opacity .25s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
body {
  margin: 0px;
  font-family: 'Roboto', sans-serif;
}
.display {
  text-align: center;
  background: -webkit-linear-gradient(to right, #00c187, #2e475f);
  background: linear-gradient(to right, #00c187, #2e475f);
  display: inline-block;
  width: 100%;
  color: white;
}
@keyframes spinner {
to {
    transform: rotate(360deg);
}
}
.spinner-wrapper {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
}
.spinner {
  background: #f7f7f7;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -20px;
  margin-left: -20px;
  box-sizing: border-box;
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
.spinner:before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -13px;
  margin-left: -13px;
  box-sizing: border-box;
  display: inline-block;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  border: 2px solid #00c187;
  border-top-color: #f7f7f7;
  animation: spinner .6s linear infinite;
}
.title {
  font-weight: 900;
  font-size: 70px;
}
.select {
  display: inline-block;
  font-weight: 300;
  font-size: 14px;
}
.subtitle {
  font-size: 28px;
  font-weight: 100;
  margin-bottom: 60px;
  margin-top: -2em;
  color: rgba(255, 255, 255, 0.69);
}
.user-detail {
  display: inline-block;
  background: rgba(0, 0, 0, 0.68);
  padding: 0px 40px;
  margin-bottom: 30px;
  border-radius: 3px;
}
.user-detail h2, .user-detail h3 {
    font-weight: 100;
}
.photo {
  border-radius: 50%;
  height: 200px;
  width: 200px;
}
.user-list {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0px 20px;
}
.user-list h2 {
    color: #2c4d61;
}
.user-list ul {
    padding: 0px;
}
.user {
  list-style: none;
  display: inline-block;
  background: rgba(255, 0, 0, 0.23);
  margin: 0px 3px 6px;
  padding: 3px 9px;
  font-size: 16px;
  border-radius: 2px;
  color: #040404;
  font-weight: 300;
  transition: all .15s linear;
}
.user:not(.inactive) {
    cursor: pointer;
    background: rgba(45, 73, 95, 0.25);
}
.user:not(.inactive):hover {
      background: rgba(45, 73, 95, 0.45);
}



